/* Outer section - full width */
#SuccessStories {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  background: linear-gradient(180deg, #ffffff, #f9fafb);
  padding: 35px 20px;
  min-height: 100vh;
}

.section-success-stories {
  max-width: 1200px;
  margin: 0 auto;
}

.section-success-stories h1 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #2d3748;
  font-weight: 800;
  line-height: 1.2;
}

.success-section-description {
  text-align: center;
  font-size: 1.25rem;
  color: #64748b;
  max-width: 800px;
  margin: 0 auto 64px;
  line-height: 1.6;
}

.success-divider {
  height: 2px;
  background: linear-gradient(to right, transparent, #3498db, transparent);
  width: 100px;
  margin: 0 auto 20px;
}

.success-stories-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
  justify-items: center;
}

.success-story-card {
  background: #f9f9fa;
  border-radius: 16px;
  padding: 30px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.06);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  width: 346.66px;
  position: relative;
  min-height: 300px;
}

.success-story-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
}

.success-quote-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  opacity: 1;
  visibility: visible;
}

.success-quote-icon img {
  width: 100%;
  height: 40px;
  fill: #e5e7eb;
}

.success-rating-stars {
  color: #facc15;
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.success-testimonial-text {
  color: #98a1b3;
  line-height: 1.6;
  margin-bottom: 20px;
  font-size: 1.05rem;
  font-style: italic;
  position: relative;
  padding: 0 5px;
  flex-grow: 1;
}

.success-divider-small {
  height: 1px;
  width: 100%;
  background-color: #e5e7eb;
  margin: 16px 0;
}

.success-user-info {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.success-user-info h3 {
  color: #2a303c;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 5px;
}

.success-user-stats {
  background: linear-gradient(to right, #16a34a, #22c55e);
  color: #ffffff;
  font-size: 14px;
  border-radius: 9999px;
  padding: 6px 12px;
  width: fit-content;
  margin-bottom: 8px;
  font-weight: 500;
}

.success-user-title {
  color: #6b7280;
  font-size: 14px;
  font-weight: 400;
}

/* Shared Story Styling */
.success-shared-story {
  border: 2px solid #3b82f6;
  position: relative;
  animation: successFadeInUp 0.6s ease-out;
}

.success-shared-story::before {
  content: "Shared from Community";
  position: absolute;
  top: -10px;
  right: 20px;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 600;
  z-index: 1;
  animation: successSlideInFromRight 0.8s ease-out 0.2s both;
}

/* Animation for new stories */
@keyframes successFadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes successSlideInFromRight {
  from {
    opacity: 0;
    transform: translateX(20px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.success-stats-section {
  background: white;
  border-radius: 12px;
  padding: 40px;
  text-align: center;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
}

.success-stats-section h2 {
  color: #98a1b3;
  margin-bottom: 30px;
  font-size: 1.8rem;
  font-weight: 600;
}

.success-stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 30px;
}

.success-stat-item {
  padding: 15px;
  width: 180px;
}

.success-stat-number {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.success-stat-label {
  color: #98a1b3;
  font-size: 0.9rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .success-stories {
    padding: 40px 15px;
  }

  .success-stories h1 {
    font-size: 2rem;
  }

  .success-stories-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .success-stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  .success-stat-number {
    font-size: 2rem;
  }

  .success-stats-section {
    padding: 25px;
  }
}

@media (max-width: 480px) {
  .success-stats-grid {
    grid-template-columns: 1fr;
  }

  .success-story-card {
    padding: 20px;
    width: 100%;
  }
}