.tm-container {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    padding: 24px;
    box-sizing: border-box;
}

.tm-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100px;
    margin-bottom: 32px;
}

.tm-title-row {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0px;
}

.tm-header-icon {
    width: 24px;
    height: 24px;
    margin-top: 4.5px;
    margin-right: 10px;
}

.tm-title {
    font-size: 28px;
    font-weight: 700;
    color: #2B2F3B;
    margin: 0;
    letter-spacing: -0.2px;
}

.tm-subtitle {
    font-size: 15px;
    color: #52607a;
    margin: 6px 0 0 0;
    text-align: center;
    max-width: 760px;
}

.tm-top {
    display: flex;
    gap: 40px;
    align-items: center;
    justify-content: center;
    margin-bottom: 32px;
    flex-wrap: wrap;
}

.tm-add-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    background: linear-gradient(101deg, #3C83F6 0%, #6DA2F8 100%);
    color: #FAFAFA;
    border: none;
    border-radius: 8px;
    padding: 12px 20px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    margin: 0 auto 32px auto;
    transition: all 0.2s ease;
    box-shadow: 0 2px 4px rgba(79, 125, 243, 0.2);
}

.tm-add-btn:hover {
    background: linear-gradient(101deg, #3C83F6 50%, #6DA2F8 150%);
    transform: translateY(-1px);
    box-shadow: 0 4px 8px rgba(79, 125, 243, 0.3);
}

.tm-add-icon {
    font-size: 18px;
    font-weight: 400;
}

/* Tablet styles */
@media (max-width: 1024px) {
    .tm-container { padding: 20px; max-width: 720px; }
    .tm-header { margin-bottom: 28px; }
    .tm-header-icon { width: 22px; height: 22px; margin-top: 2px; margin-right: 8px; }
    .tm-title { font-size: 26px; }
    .tm-subtitle { font-size: 15px; }
    .tm-add-btn { padding: 12px 18px; font-size: 14px; }
}

/* Mobile styles */
@media (max-width: 768px) {
    .tm-container { padding: 16px; }
    .tm-header { margin-bottom: 24px; }
    .tm-header-icon { margin-top: 2px; margin-right: 0px; }
    .tm-title { font-size: 24px; }
    .tm-subtitle { font-size: 14px; margin-top: 4px; }
    .tm-top { flex-direction: column; margin-bottom: 24px; gap: 24px; }
    .tm-add-btn { width: 100%; margin-bottom: 20px; padding: 14px 20px; font-size: 15px; justify-content: center; }
}

/* Small mobile styles */
@media (max-width: 480px) {
    .tm-container { padding: 12px; }
    .tm-header { margin-bottom: 20px; }
    .tm-header-icon { width: 20px; height: 20px; margin-top: 2px; margin-right: 4px; }
    .tm-title { font-size: 20px; }
    .tm-subtitle { font-size: 13px; }
    .tm-add-btn { padding: 12px 16px; font-size: 14px; } 
}

/* Extra small mobile styles */
@media (max-width: 375px) {
    .tm-header-icon { margin-top: 2px; margin-right: 2px; }
    .tm-title { font-size: 20px; }
    .tm-subtitle { font-size: 12px; }
}